import React, { Component } from 'react'
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {ProductConsumer} from '../context'
import { FaCartPlus } from 'react-icons/fa';
import PropTypes from 'prop-types';



export default class Medicine extends Component {
    render() {

        const{id , title,img ,price , inCart} = this.props.medicine;
        return (
            <ProductWrapper className = "col-9 mx-auto col-md-6 col-lg-3 my-3">
            <div className="card">
            <ProductConsumer>
                        {(value) => (<div className="img-container p-5 " onClick={() => value.handleDetail(id)}>
                            <Link to="/details">
                                <img src={img} alt="Medicine" height="400px" width="500px" className="card-img-top" />
                            </Link>
                            <button
                                className="cart-btn"
                                disabled={inCart ? true : false}
                                onClick={() =>{value.addToCart(id);
                                               value.openModal(id);}}
                            >
                                {inCart ? (
                                    <p className="text-capitalize mb-0" background-color="green" disabled>{" "}in-cart</p>) : <FaCartPlus size="25px" color="green" />
                                }
                            </button>
                        </div>)}

            
            
                    </ProductConsumer>
             <div className= "card-footer d-flex justify-content-between bg-success">
                        <p className="align-self-center mb-0"><strong>{title}</strong></p>
             <h5 className="text-black font-italic mb-0">
             <span className ="mr-1">₹</span>
             {price}
             </h5>
             </div>
             </div>
</ProductWrapper>
              
        )
    }
}

Medicine.propTypes = {

    medicine : PropTypes.shape({

        id : PropTypes.number,
        img : PropTypes.string ,
        title : PropTypes.string,
        price : PropTypes.number ,
        inCart : PropTypes.bool
    }).isRequired

};


const ProductWrapper = styled.div`

.card{

    border-color:transparent;
    transition : all 0.1s linear ;
}
.card-footer{

    transition: all 0.1s linear;
    }

 &:hover{
.card{
    border: 0.4rem solid rgba(0,0,0.2);
    box-shadow:2px 2px 5px rgba(0,0,0.2);
}

 }

 .img-container{
     position : relative;
     overflow : hidden ;
}
.card-img-top{
    transition: all 0.4s linear;
}

.img-container:hover .card-img-top{

    transform: scale(1.2);
}
.cart-btn{

    position: absolute ;
    top : right ;
    bottom : 0 ;
    border : none ;
    border-radius : 0.5rem 0 0 0;
    transform : translate(100% ,100%);
    transition : all 0.5s linear ;

}

.img-container:hover .cart-btn{
    transform : translate(0,0);
}


`;